import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Navbar from '../auth/elements/Navbar-Pagina';
import Noticia from '../auth/elements/Div-Noticias';
import NoticiaTop from '../auth/elements/Div-Noticias-Top';
import NoticiaAd from '../auth/elements/Anuncio';
import FooterList from '../auth/elements/Footer-List';
import Footer from '../auth/elements/Footer';
import Pagination from '@material-ui/lab/Pagination';



const useStyles = makeStyles(theme => ({
  root: {
    '& > *': {
      marginTop: theme.spacing(3),
    },
  },
  paper: {
    padding: theme.spacing(4),
    textAlign: 'center',
    boxShadow: 'null',
    color: theme.palette.text.secondary,
  },
}));

export default function Home() {
  const classes = useStyles();

  return (
    <div >
    <nav>
      <Navbar />
    </nav><div className="grid"><Grid container spacing={2}>
        <header className="home-header">
        <Grid item xs={12}>
          <h6>HOME</h6>
        </Grid>
        </header>

        <Grid item xs={12} sm={3}>
          <NoticiaAd />
          <NoticiaAd />
          <NoticiaAd />
          <NoticiaAd />
        </Grid>

        <Grid item xs={12} sm={6}>
          <Noticia />
          <Noticia />
          <Noticia />
          <Noticia />
          <div id="pagination" className={classes.root}>
            <Pagination count={10} color="primary" />
          </div>

        </Grid>

        <Grid item xs={12} sm={3}>
          <NoticiaTop />
          <NoticiaTop />
          <NoticiaTop />
          <NoticiaTop />
        </Grid>

      </Grid></div>
      

      <footer>
        <FooterList />
        <Footer/>
      </footer>
    </div>
  );
}
