import React, {Fragment, useState, useContext} from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import AlertaContext from '../../../context/alertas/alertaContext';
import {Link} from 'react-router-dom';


function Login() {

    /*extraer valores del context */
    const alertaContext = useContext(AlertaContext);
    const {alerta, mostrarAlerta} = alertaContext;

    /* state para iniciar sesión */
    const [usuario, guardarUsuario] = useState ({
        email:'',
        password:''
    })

    /*extraer */
    const {email, password} = usuario;

    const onChange = e => {
        guardarUsuario({
            ...usuario,
            [e.target.name] : e.target.value
        })
    }

    /**/
    const onSubmit = e => {
        e.preventDefault();

        //validar que no haya campos
        if(
        email.trim() === ''||
        password.trim() === ''){
            mostrarAlerta('Todos los campos son obligatorios', 'alerta-error')
        }
        //pasarlo al action
        
    }


    return (
        <Fragment>
        
        <form className="login-form" noValidate autoComplete="off" onSubmit={onSubmit} >
            <div className="login-grid">
            <Typography variant="h3" >
            Ingresa aquí 
            </Typography>
            {alerta ? ( <div className={`alerta ${alerta.categoria}`}>{alerta.msg}</div> ): null}
            <div className="login-div">
            <TextField className="field-usuario" value={email} onChange={onChange} 
            variant="outlined" id="outlined-basic" label="Nombre de usuario/Correo electrónico" type="email" name="email"/>
            </div>
            <div className="login-div">
            <TextField className="field-usuario" value={password} onChange={onChange}  
            variant="outlined" id="outlined-basic"  label="Contraseña" type="password" name="password"/>
            </div>
            <div className="login-div">
            <Button type="submit" className="boton-login" variant="contained" c color="primary" name="go-user-login">
            Entrar
            </Button>
            </div>
            <div className="login-div">
            <Typography variant="caption" >
            Si no tienes una cuenta 
            </Typography>

            <Link to="/sign-in" id="enlace-cuenta">
            <Typography  variant="overline" display="block"  id="enlace-cuenta">
                dale aquí para registrarte.
            </Typography></Link>
            </div></div>
            
        </form>
        </Fragment>
        
    );
}

export default Login;