import React, { Fragment, useState, useContext } from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import LogoPescado from './logo-pescado.png';
import { Icon } from '@material-ui/core';
import AlertaContext from '../../../context/alertas/alertaContext';
import AlertaState from '../../../context/alertas/alertaState';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';

const useStyles = makeStyles(theme => ({
    root: {
      flexGrow: 2,
    },
    paper: {
      padding: theme.spacing(2),
      textAlign: 'center',
      boxShadow: 'null',
      color: theme.palette.text.secondary,
    },
  }));

function SignIn() {

    /*extraer valores del context */
    const alertaContext = useContext(AlertaContext);
    const {alerta, mostrarAlerta} = alertaContext;


    /* state para crearusuario */
    const [usuario, crearUsuario] = useState ({
        nombre:'',
        apellidos:'',
        nombreusuarios:'',
        email:'',
        password:'',
        edad:''
    })

    /*extraer */
    const {nombre, apellidos, nombreusuario, email, password, edad} = usuario;

    const onChange = e => {
        crearUsuario({
            ...usuario,
            [e.target.name] : e.target.value
        })
    }
         


    /**/
    const onSubmit = e => {
        e.preventDefault();
        

        //validar que no haya campos
        if(nombre.trim() === '' ||
        apellidos.trim() === '' ||
        nombreusuario.trim() === ''||
        email.trim() === ''||
        password.trim() === ''||
        edad.trim() === ''){
            mostrarAlerta('Todos los campos son obligatorios','alerta-error')
        }else if(password.trim().length  < 6) {
            mostrarAlerta('Contraseña minimo 6 caracteres','alerta-error')
        }else if(!/^[A-Z]/.test(password.trim())) {
            mostrarAlerta('Contraseña debe tener minimo UNA mayuscula','alerta-error')
        }else if(!/(?=.*?[0-9])/.test(password.trim())) {
            mostrarAlerta('Contraseña debe tener minimo UN numero','alerta-error')
        }else if(/[a-zA-Z]/.test(edad.trim())) {
            mostrarAlerta('Edad solo acepta números','alerta-error')
        }else if(!/^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(email.trim())){
            mostrarAlerta('Formato invalido de Email','alerta-error')
        }


        //pasarlo al action

        
    }

    const classes = useStyles();

    return (
        <Fragment>
        <form className="logo-sign-form" noValidate autoComplete="off">
            <img src={LogoPescado} alt="HTML5 Icon" className="logo-signin"/>
        </form>

        <form className="signin-form" onSubmit={onSubmit}  noValidate autoComplete="off">
            <img src={LogoPescado} alt="HTML5 Icon" className="logo-signin-res"/>
            <div className="signin-div">
            <h6 >BIENVENIDO 
            </h6>
            </div>
            {alerta ? ( <div className={`alerta ${alerta.categoria}`}>{alerta.msg}</div> ): null}
            <div className="signin-div">
            <TextField className="field-usuario" id="outlined-basic" 
            label="Nombre (s) " variant="outlined"name="nombre"value={nombre} onChange={onChange} />
            </div>
            <div className="signin-div">
            <TextField className="field-usuario" variant="outlined"id="outlined-basic"  
            label="Apellido (s)" name="apellidos"value={apellidos} onChange={onChange} />
            </div>
            <div className="signin-div">
            <TextField className="field-usuario" variant="outlined"id="outlined-basic" 
            label="Nombre de usuario" name="nombreusuario"value={nombreusuario} onChange={onChange} />
            </div>
            <div className="signin-div">
            <TextField className="field-usuario"variant="outlined" id="outlined-basic" 
            label="Correo electrónico" name="email"value={email} onChange={onChange} />
            </div>
            <div className="signin-div">
            <TextField className="field-usuario"variant="outlined" id="outlined-basic"  
            label="Contraseña" type="password" name="password"value={password} onChange={onChange} />
            </div>
            <div className="signin-div">
            <TextField className="field-usuario"variant="outlined" id="outlined-basic"  
            label="Edad" name="edad"value={edad} onChange={onChange}  />
            </div>


            <div className="signin-div">
            <Button type="submit" id="boton-signin"variant="contained" c color="primary" name="go-user-login">
            Registrar
            </Button>
            </div>
            
        </form>
        
    </Fragment>
    );
}

export default SignIn;