import React from 'react';
import { fade, makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import {Link} from 'react-router-dom';
import InputBase from '@material-ui/core/InputBase';
import SearchIcon from '@material-ui/icons/Search';
import MenuResponsive from './Menu-Responsive';
import InstagramIcon from '@material-ui/icons/Instagram';
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(1),
  },
  title: {
    flexGrow: 0.85,
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(1),
      width: 'auto',
    },
  },
  searchIcon: {
    width: theme.spacing(7),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'primary',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 7),
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: 20,
      '&:focus': {
        width: 100,
      },
    },
  },
}));

export default function ButtonAppBar() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <AppBar position="static" id="appbar-footer">
        <Toolbar>
          <Typography variant="h6" id="home-footer" className={classes.menuButton}>
            PAGE
          </Typography>
          <Typography variant="h6" id="logo-nav" className={classes.title}>
            
          </Typography>
          <Button color="inherit" id="footer-elements">Blog</Button>
          <Button color="inherit" id="footer-elements">Acerca de</Button>
          <Button color="inherit" id="footer-elements">Contactanos</Button>
          <Button color="inherit" id="footer-elements"><Link to="/" id="nav-elements">Cerrar sesión</Link></Button>
          
          <InstagramIcon id="footer-icons-elements"/>
          <TwitterIcon id="footer-icons-elements"/>
          <FacebookIcon id="footer-icons-elements"/>
        </Toolbar>
      </AppBar>
    </div>
  );
}
